import axios from 'axios';
import {
  ref,
  computed
} from 'vue';
import {
  API_BASE_URL
} from '@/config';

/*
получение данных о цветах, категориях, ...

*/
export default function filterList() {
  const fetchStatus = {
    isLoading: ref(false),
    isLoadingFailed: ref(false)
  }

  const colors = ref([]);
  const categories = ref([]);
  const materials = ref([]);
  const seasons = ref([]);

  fetchStatus.isLoading.value = true;
  fetchStatus.isLoadingFailed.value = false;
  axios.get(`${API_BASE_URL}api/colors`)
    .then((response) => {
      colors.value = response.data
    })
    .catch((error) => {
      fetchStatus.isLoadingFailed.value = true;
    })
    .then(() => {
      fetchStatus.isLoading.value = false;
    })

  fetchStatus.isLoading.value = true;
  fetchStatus.isLoadingFailed.value = false;
  axios.get(`${API_BASE_URL}api/productCategories`)
    .then((response) => {
      categories.value = response.data
    })
    .catch((error) => {
      fetchStatus.isLoadingFailed.value = true;
    })
    .then(() => {
      fetchStatus.isLoading.value = false;
    })

  fetchStatus.isLoading.value = true;
  fetchStatus.isLoadingFailed.value = false;
  axios.get(`${API_BASE_URL}api/materials`)
    .then((response) => {
      materials.value = response.data
    })
    .catch((error) => {
      fetchStatus.isLoadingFailed.value = true;
    })
    .then(() => {
      fetchStatus.isLoading.value = false;
    })

  fetchStatus.isLoading.value = true;
  fetchStatus.isLoadingFailed.value = false;
  axios.get(`${API_BASE_URL}api/seasons`)
    .then((response) => {
      seasons.value = response.data
    })
    .catch((error) => {
      fetchStatus.isLoadingFailed.value = true;
    })
    .then(() => {
      fetchStatus.isLoading.value = false;
    })

  return {
    colors,
    categories,
    materials,
    seasons,
    filterList,
    status: fetchStatus,
  };
}
