<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <label class="form__label" :for="nameInput">
    <input class="form__input" :type="type" :name="nameInput" :id="nameInput"
      :placeholder="placeholderCenter" @input="check($event)">
    <span class="form__value">{{ placeholder }}</span>
    <span class="form__error" v-if="error">{{ error }}</span>
  </label>
</template>
<script setup>
import { toRefs,computed, ref } from 'vue'
const props = defineProps({
  placeholder: {
    type: String,
    required: true
  },
  placeholderCenter: {
    type: String,
  },
  error: {
    type: String,
  },
  nameInput: {
    type: String
  },
  type: {
    type: String
  }
})
const emit = defineEmits(['update'])
const { placeholder, placeholderCenter, error, nameInput, type } = toRefs(props);


const check = (params) => {
  emit('update', params.target.value);
}
</script>
