<template>
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ countProductsStr }}
        </span>
      </div>
    </div>

    <div class="content__catalog">
      <div class="content__catalog-wrap">
        <aside class="button">
          <h2 class="button__title">Количество товаров на станице</h2>
          <div class="button-wrap">
            <ul class="button-list">
              <li v-for="item in countProductsArray" :key="item">
                <label :for="item" class="button-label" :class="{ 'button-checked': limit === item }">
                  <input type="radio" name="accept-offers" :id="item" class="hidden radio-label" :value="item"
                    v-model.number="limit">
                  <span class="button-span">{{ item }}</span>
                </label>
              </li>
            </ul>
          </div>
        </aside>

        <aside class="filter" v-if="filterStatus.isLoading">
          <h2 class="filter__title">Фильтры</h2>
          <form class="filter__form form" action="#" method="get"
            @submit.prevent="loadProducts(page, limit, minPrice, maxPrice, colorIds, categoryId, materialIds, seasonIds)">
            <CatalogFilter :categories="categories" :materials="materials" :seasons="seasons"
              :select-category="categoryId" @send="paramsSend($event)" />
            <button class="filter__submit button button--primery" type="submit">
              Применить
            </button>
            <button class="filter__reset button button--second" type="reset" @click="resetParams"
              :style="{ 'visibility': visibleReset }">
              Сбросить
            </button>
          </form>
        </aside>
      </div>
      <section class="catalog">
        <CatalogList :products="products" :product-load="productStatus.isLoading"
          :product-load-failed="productStatus.isLoadingFailed" />
        <Base-paginate @change-page="page = $event" :page="page" :count-all-products="countProducts"
          :count-in-page="limit" />
      </section>
    </div>
  </main>
</template>
<script setup>
import { ref, computed, watch } from 'vue';

import CatalogFilter from '@/components/layout/CatalogFilter.vue';
import CatalogList from '@/components/layout/CatalogList.vue';
import BasePaginate from '@/components/layout/BasePaginate.vue';
import { productList } from '@/hooks/useProduct.js';
import useFilters from '@/hooks/useFilters.js';
import selectionEnd from '@/helpers/selectionEnd.js';
import { useRoute, useRouter, onBeforeRouteUpdate } from 'vue-router';

const { filterList, categories, materials, seasons, status: filterStatus } = useFilters();
filterList();

let { loadProducts, productData, status: productStatus } = productList();

const $route = useRoute();
const $router = useRouter();

const countProductsArray = [9, 18, 27]
const page = ref(1);
const limit = ref(9);
const minPrice = ref(null);
const maxPrice = ref(null);
const colorIds = ref([]);
let categoryId = ref(0);
if (+$route.params.categoryId) { categoryId = ref(+$route.params.categoryId); }
const materialIds = ref([]);
const seasonIds = ref([]);

const paramsSend = (event) => {
  minPrice.value = event[0].value;
  maxPrice.value = event[1].value;
  categoryId.value = event[2].value;
  materialIds.value = event[3].value;
  seasonIds.value = event[4].value;
}
const resetParams = () => {
  minPrice.value = null;
  maxPrice.value = null;
  colorIds.value = [];
  categoryId.value = 0;
  materialIds.value = [];
  seasonIds.value = [];
  $router.push({
    name: 'main',
  });
  loadProducts(page.value, limit.value, minPrice.value, maxPrice.value, colorIds.value, categoryId.value, materialIds.value, seasonIds.value);
}

loadProducts(page.value, limit.value, minPrice.value, maxPrice.value, colorIds.value, categoryId.value, materialIds.value, seasonIds.value);

const products = computed(() => {
  const res = productData.value
    ? productData.value.items.map((product) => ({
      ...product,
    }))
    : [];
  return res;
})

const visibleReset = computed(() => {
  return (minPrice.value === null && maxPrice.value === null && (categoryId.value === 0 || categoryId.value === null) && materialIds.value.length === 0 && seasonIds.value.length === 0) ? 'hidden' : 'visible'
})

const countProductsStr = computed(() => {
  if (productData.value) {
    return productData.value.pagination ? selectionEnd(productData.value.pagination.total, ['товар', 'товара', 'товаров']) : '0 товаров';
  }
  return '0 товаров';
})

const countProducts = computed(() => {
  if (productData.value) {
    return productData.value.pagination ? productData.value.pagination.total : 0;
  }
  return 0;
})
watch(page, () => {
  loadProducts(page.value, limit.value, minPrice.value, maxPrice.value, colorIds.value, categoryId.value, materialIds.value, seasonIds.value);
})

watch(limit, () => {
  loadProducts(page.value, limit.value, minPrice.value, maxPrice.value, colorIds.value, categoryId.value, materialIds.value, seasonIds.value);
})

watch(categoryId, () => {
  loadProducts(page.value, limit.value, minPrice.value, maxPrice.value, colorIds.value, categoryId.value, materialIds.value, seasonIds.value);
})

onBeforeRouteUpdate(() => {
  loadProducts(page.value, limit.value, minPrice.value, maxPrice.value, colorIds.value, categoryId.value, materialIds.value, seasonIds.value);
})

</script>
