import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import {
  ref,
  computed
} from 'vue';
import {
  API_BASE_URL
} from '@/config';
/*

получение данных о товарах

*/

export function productList() {
  const productData = ref(null);

  const fetchStatus = {
    isLoading: ref(false),
    isLoadingFailed: ref(false)
  }

  const fetchProducts = (page, limit, minPrice, maxPrice, colorIds = [], categoryId,
    materialIds = [], seasonIds = []) => {
    fetchStatus.isLoading.value = true;
    fetchStatus.isLoadingFailed.value = false;
    axios.get(`${API_BASE_URL}api/products`, {
        params: {
          page: page,
          limit: limit,
          minPrice: minPrice,
          maxPrice: maxPrice,
          categoryId: categoryId,
          materialIds: materialIds,
          seasonIds: seasonIds,
          colorIds: colorIds,
        },
      })
      .then((response) => {
        // handle success
        productData.value = response.data;
      })
      .catch((error) => {
        fetchStatus.isLoadingFailed.value = true;
        //console.log(error);
      })
      .then(() => {
        fetchStatus.isLoading.value = false;
      })
      .finally(() => {});
  };

  return {
    productData,
    loadProducts: fetchProducts,
    status: fetchStatus,
  };
}

/*

получение информации о товаре

*/
export function productItem() {
  const productData = ref(null);
  const category = computed(() => {
    return productData.value ? productData.value.category : {};
  });
  const fetchStatus = {
    isLoading: ref(false),
    isLoadingFailed: ref(false)
  }
  const fetchProduct = async (productId) => {
    fetchStatus.isLoading.value = true;
    fetchStatus.isLoadingFailed.value = false;
   await  axios.get(`${API_BASE_URL}api/products/${productId}`)
      .then((response) => {
        const product = response.data;
        productData.value = Object.assign(product, {
          pricePretty: numberFormat(product.price)
        });
      })
      .catch((error) => {
        fetchStatus.isLoadingFailed.value = true;
      })
      .then(() => {
        fetchStatus.isLoading.value = false;
      })
      .finally(() => {});

  };

  return {
    productData,
    category,
    loadProduct: fetchProduct,
    status: fetchStatus
  };
}
