<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section class="cart">
      <!-- <form class="cart__form form" action="#" method="POST"> -->
      <form class="cart__form form" action="#" method="POST" @submit.prevent="clickOnButton">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText type="text" placeholder="ФИО" :error="formError.name"
              placeholderCenter="Введите ваше полное имя" v-model="formData.name" nameInput="name"
              @update="formData.name = $event" />

            <BaseFormText type="text" placeholder="Адрес доставки" :error="formError.address"
              placeholderCenter="Введите ваш адрес" v-model="formData.address" nameInput="address"
              @update="formData.address = $event" />

            <BaseFormText type="tel" placeholder="Телефон" :error="formError.phone"
              placeholderCenter="Введите ваш телефон" v-model="formData.phone" nameInput="phone"
              @update="formData.phone = $event" />

            <BaseFormText type="email" placeholder="Email" :error="formError.email" placeholderCenter="Email"
              v-model="formData.email" nameInput="email" @update="formData.email = $event" />{{ formError.email }}

            <BaseFormTextAria placeholder="Комментарий к заказу" :error="formError.comment"
              placeholderCenter="Ваши пожелания" v-model="formData.comment" nameInput="comment"
              @update="formData.comment = $event" />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options" v-if="statusDelivery.isLoading">
              <li class="options__item" v-for="item in deliveries" :key="item.id">
                <label class="options__label" :for="item.deliveryFor">
                  <input class="options__radio sr-only" type="radio" :id="item.deliveryFor" name="delivery"
                    :value="item.id" v-model="deliveryTypeId">
                  <span class="options__value">
                    {{ item.title }} <b v-if="(item.price === '0')">бесплатно</b><b v-else>{{ item.sum }} ₽</b>
                  </span>
                </label>
              </li>
            </ul>
            <label class="form__label">
            <span class="form__error-pay" v-if="formError.paymentTypeId">{{ formError.paymentTypeId }}</span>
            </label>
            <BasePay v-if="statusDelivery.isLoading" @update="paymentTypeId = $event" :deliveryId="deliveryTypeId"
              :key="deliveryTypeId" />

          </div>
        </div>

        <OrderStructure v-if="statusDelivery.isLoading" :cartSend="status.isSend.value"
          :cartSendFailed="status.isSendFailed.value" :cartSending="status.isSending.value"
          :price-delivery="deliverySum" />

        <div class="cart__error form__error-block" v-if="formErrorMessange">

          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessange }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script setup>
import { computed, ref, watch } from 'vue'
import BaseFormText from '@/components/layout/BaseFormText.vue';
import BaseFormTextAria from '@/components/layout/BaseFormTextAria.vue';
import OrderStructure from '@/components/layout/OrderStructure.vue';
import { sendOrder, deliveryList, payList } from '../hooks/useOrder.js'
import BasePay from '@/components/layout/BasePay.vue'
const formData = ref({});
const { formError, formErrorMessange, fetchOrder: order, status } = sendOrder();

const { deliveries, statusDelivery } = deliveryList();

const deliverySum = computed(() => {
  return deliveries.value[deliveryTypeId.value - 1] ? Number(deliveries.value[deliveryTypeId.value - 1].price) : 0

})

const deliveryTypeId = ref(1);
const paymentTypeId = ref(null);

const clickOnButton = () => {
  formData.value.deliveryTypeId = deliveryTypeId.value;
  formData.value.paymentTypeId = paymentTypeId.value;
  order(formData.value)
}
</script>
<style scoped>
.form__error-pay{
  position: absolute;
    bottom: -32px;
    left: 20px;
    font-size: 11px;
    line-height: 14px;
    color: #e02d71;
}
</style>
