<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="imageItem" width="120" height="120" :alt="item.products.title">
    </div>
    <h3 class="product__title">
      {{ item.products.title }}
    </h3>
    <p class="product__size">
      Размер: {{ item.sizeId }}
    </p>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ 'background-color': item.colorCode }"></i>
        {{ colorNameRus }}
      </span>
    </p>
    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>

    <base-addremovefromstore :item="item" />

    <b class="product__price">
      {{ totalPricePretty }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProductfromCart(item.basketItemId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
<script setup>
import BaseAddremovefromstore from '@/components/layout/BaseAddremovefromstore.vue';
import numberFormat from '@/helpers/numberFormat';
import { computed, toRefs, ref } from 'vue'
import { useStore } from 'vuex';
import imgEmty from '@/assets/img/ab21d54aac6d7640e57e.png'
const $store = useStore();
const props = defineProps({
  item: {
    type: Object,
  },
})
const { item } = toRefs(props)

const imageItem = computed(() => {
  return item.value.products.image || imgEmty;
})

let colorNameRus = ref('')
const tableColors = { Red: 'Красный', Blue: 'Синий', Green: 'Зеленый', Yellow: 'Желтый', Purple: 'Фиолетовый', Pink: 'Розовый', black: 'Черный', white: 'Белый' }
for (var key in tableColors) {
  if (key == item.value.colorTitle) {
    colorNameRus.value = tableColors[key];
    break;
  }
}

const totalPricePretty = computed(() => {
  return numberFormat(item.value.products.price * item.value.count);
})
const deleteProductfromCart = (basketItemId) => $store.dispatch('deleteProductfromCart', basketItemId);

</script>
<style scoped>
.product {
  display: grid;
  grid-template-columns: 120px 280px 140px auto 20px;
  grid-template-rows: repeat(4, min-content);
  align-items: center;
  grid-gap: 10px 20px;
}
.product__size{
  margin: 0;
    padding: 0;
    grid-column: 2/3;
    grid-row: 4/5;
    font-size: 12px;
    line-height: 100%;
    font-weight: 200;
}
</style>
