<template>
  <label :class="classLabel" :for="colorValueText">
    <input :class="classInput" type="radio" :name="nameRadio" :value="colorId" :id="colorValueText"
      @input="radioClick($event)" :checked="curColor === colorId">
    <span :class="classSpan" :style="{ 'background-color': colorValueRgb }"></span>
  </label>
</template>
<script setup>
import { toRefs, computed } from 'vue'
const emits = defineEmits(['updateRadioGroup'])
const props = defineProps({
  colorId: {
    type: Number
  },
  colorValueText: {
    type: String,
    default: ''
  },
  colorValueRgb: {
    type: String,
    default: ''
  },
  curColor: {
    type: Number,
  },
  curIndex: {
    type: Number
  },
  productId: {
    type: Number
  },
  nameRadio:{
    type:String
  },
  classLabel:{
    type:Array
  },
  classInput:{
    type:Array
  },
  classSpan:{
    type:Array
  }
})

const { colorId, colorValueText, colorValueRgb, curColor, productId } = toRefs(props)

const radioClick = (event) => {
  emits('updateRadioGroup', event.target.value)
}
</script>
