<template>
  <Teleport class="teleport" to="#teleport-target" v-if="open">
    <div class="teleport-blackout"></div>
    <div class="teleport-modal" @click="onOutsideClick">
      <div class="teleport-modal__content" ref="contentModal">
        <button type="button" class="teleport-modal__close" @click="doCloseModal">X</button>
        <slot></slot>
      </div>
    </div>
  </Teleport>
</template>
<script setup>
import { defineProps, defineEmits, ref, watch, onMounted, onUnmounted } from 'vue';
import useModal from '../../hooks/useModal.js'
const prop = defineProps({
  open: {
    type: Boolean
  },
})

const { isOpen, isSomeOpen, doOpen, doClose } = useModal();
const emit = defineEmits(['update:open']);
const contentModal = ref(null);

const doCloseModal = () => {
  emit('update:open', false);
}
const onOutsideClick = (event) => {
  if (event.target !== contentModal.value && event.target.contains(contentModal.value)) {
    doCloseModal();
  }
}

onMounted(() => {
  window.addEventListener('keydown', escCloseModal);
})
onUnmounted(() => {
  window.removeEventListener('keydown', escCloseModal);
})

const escCloseModal = (e) => {
  if (prop.open && e.key === 'Escape') {
    doCloseModal();
  }
}

watch(() => prop.open, (isOpen) => {
  if (isOpen) {
    doOpen();
  }
  else {
    doClose();
  }
}, { immediate: true })
if (prop.open) {
  doOpen();
}

</script>
<style scoped>
.teleport-blackout {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.75;
  background-color: #000;
}

.teleport-modal {
  z-index: 1000;
  overflow: auto;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
}

.teleport-modal__content {
  position: relative;
  max-width: 1000px;
  margin-top: auto;
  margin-bottom: auto;
  padding: 40px;
  background-color: #fff;
}

.teleport-modal__close {
  position: absolute;
  top: -1.25em;
  right: -1.25em;
  padding: 10px;
  line-height: 1;
}
</style>
