<template>
  <div class="cart__block" v-if="productCalc">
    <ul class="cart__orders">
      <li v-for="item in productCalc" :key="item.productId" class="cart__order">
        <h3>
          {{ item.products.title }}
        </h3>
        <b>
          {{ numberFormat(item.count * item.products.price) }} ₽
        </b>
        <span>
          Артикул: {{ item.productId }}
        </span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b v-if="priceDelivery >0">{{priceDelivery}} ₽</b><b v-else>бесплатно</b></p>
      <p>Итого: <b>{{ totalProductCount }}</b> товара на сумму <b>
          {{ numberFormat(Number(totalPrice) + Number(priceDelivery)) }}₽</b></p>
    </div>

    <button class="cart__button button button--primery" type="submit" :disabled="cartSend">
      Оформить заказ
    </button>
  </div>
  <p v-if="cartSend">Оформление заказа...</p>
  <div class="cart__error form__error-block" v-else-if="cartSendFailed">
    <h4>Заявка не отправлена!</h4>
    <p>
      Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.
    </p>
  </div>
  <p v-else-if="cartSending">Заказ оформлен</p>
</template>
<script setup>

import { computed } from 'vue';
import numberFormat from '@/helpers/numberFormat';
import { useStore } from 'vuex';
const props = defineProps({
  cartSend: {
    type: Boolean,
  },
  cartSendFailed: {
    type: Boolean,
  },
  cartSending: {
    type: Boolean,
  },
  priceDelivery: {
    type: Number,
  },
})

const $store = useStore();

const productCalc = computed(() => $store.getters['cartDetailProducts']);
const totalPrice = computed(() => $store.getters['cartTotalPrice'])
const totalProductCount = computed(() => $store.getters['cartTotalCountProduct'])

</script>
