<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<label class="form__label">
  <textarea class="form__input form__input--area" name="comments" placeholder="Ваши пожелания"></textarea>
  <span class="form__value">Комментарий к заказу</span>
</label>
<template>
  <label class="form__label" :for="nameInput">
    <textarea class="form__input form__input--area" :id="nameInput" :name="nameInput"  @input="check($event)"
      :placeholder="placeholderCenter"></textarea>
    <span class="form__value">Комментарий к заказу</span>
  </label>
</template>
<script setup>
import { toRefs,computed, ref } from 'vue'
const emit = defineEmits(['update'])
const props = defineProps({
  placeholder: {
    type: String,
    required: true
  },
  placeholderCenter: {
    type: String,
  },
  error: {
    type: String,
  },
  nameInput: {
    type: String
  },

})

const { placeholder, placeholderCenter, error, nameInput } = toRefs(props)
const check = (params) => {
  emit('update', params.target.value);
}
</script>


