<template>
  <ul :class="classList">
    <li :class="classItem" v-for="item in productColor" :key="item.color.id">
      <base-radio :color-value-text="idRadio(item.id, item.color.id)" :color-id="item.color.id"
        :color-value-rgb="item.color.code" @updateRadioGroup="colorClick" :cur-color="currentColor"
        :product-id="productId" :name-radio="nameRadio" :class-label="classLabel" :class-input="classInput" :class-span="classSpan"/>
    </li>
  </ul>
</template>
<script setup>
import BaseRadio from '@/components/ui/BaseRadio.vue';
import { computed, toRefs } from 'vue'
const props = defineProps({
  productColor: {
    type: Array,
  },
  currentColor: {
    type: Number,
  },
  productId: {
    type: Number,
  },
  classList:{
    type:Array
  },
  classItem:{
    type:Array
  },
  classLabel:{
    type:Array
  },
  classInput:{
    type:Array
  },
  classSpan:{
    type:Array
  },
  keys:{
    type:String
  }
})

const { productId, productColor, currentColor, classList, classItem, classLabel, classInput, classSpan } = toRefs(props)

const emits = defineEmits(['radioClick']);
const colorClick = (event) => {
  emits('radioClick', +event);
}

const idRadio = (itemId, colorId) => {
  return `product_${productId.value}_${itemId}_${colorId}`
}

const nameRadio = computed(() => {
  return `color-item_${productId.value}`
})
</script>
