import axios from 'axios';
import {
  API_BASE_URL,
} from '@/config';

import {
  createStore
} from 'vuex'
const store = createStore({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartLoading: false,
    cartLoadingFailed: false,
    orderInfo: null,
    orderInfoLoading: false,
    orderInfoLoadingFailed: false,
    productAddInCart:false
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      if (orderInfo) {
        state.orderInfo = orderInfo;
      }
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmountM(state, {
      productId,
      count,
    }) {
      const items = state.cartProducts.find((item) => item.productId === productId);
      if (items) {
        items.count += count;
      }
    },
    deleteProductfromCart(state, basketItemId) {
      state.cartProducts = state.cartProducts.filter((item) => item.id !== basketItemId);
    },
    incProducttoCart(state, payload) {
      const items = state.cartProducts.find((item) => item.productId === payload.productId);
      if (items) {
        items.count += 1;
      }
    },
    decProducttoCart(state, payload) {
      const items = state.cartProducts.find((item) => item.productId === payload.productId);
      if (items && items.count > 1) {
        items.count -= 1;
      }
    },
    updateUserAccesKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, cart) {
      state.cartProductsData = cart;
    },
    updateStateCartLoading(state, value) {
      state.cartLoading = value;
    },
    updateStateCartLoadingFailed(state, value) {
      state.cartLoadingFailed = value;
    },
    updateStateAddProductInCart(state, value) {
      state.productAddInCart = value;
    },
    updateStateOrderInfoLoading(state, value) {
      state.orderInfoLoading = value;
    },
    updateStateOrderInfoLoadingFailed(state, value) {
      state.orderInfoLoadingFailed = value;
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        basketItemId: item.id,
        count: item.quantity,
        colorId: item.color.color.id,
        colorCode: item.color.color.code,
        colorTitle: item.color.color.title,
        sizeId: item.size.title,
      }));
    },
  },
  getters: {
    cartDetailProducts(state) {
      const r = state.cartProducts.map((item) => {
        const products = state.cartProductsData.find(
          (p) => p.product.id === item.productId,
        ).product;
        return {
          ...item,
          // product: state.cartProductsData.find((p) => p.id === item.product.id),
          products: {
            ...products,
            image: ///products.colors[0].gallery?products.colors[0].gallery[0].file.url:null,
              products.colors.find(c => c.color.id === item.colorId).gallery ? products
              .colors.find(c => c.color.id === item.colorId).gallery[0].file.url : null,
          },
        };
      });
      return r;
    },
    cartDetailInfo(state) {
      const r = state.orderInfo;
      // console.log('tytyt',r);
      return {
        id: r.id,
        address: r.address,
        name: r.name,
        phone: r.phone,
        email: r.email,
        basket: r.basket,
        paymentType: r.paymentType,
        deliveryPay: r.deliveryType.price,
        count: r.basket.items.length,
        sum: r.basket.items.reduce(
          (acc, item) => (item.price * item.quantity) + acc,
          0,
        ),
      };
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => (item.products.price * item.count) + acc,
        0,
      );
    },
    cartTotalCountProduct(state, getters) {
      return getters.cartDetailProducts.length;
      // return getters.cartDetailProducts.reduce(
      //   (acc, item) => (item.count) + acc,
      //   0,
      // );
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      context.commit('updateStateOrderInfoLoading', true);
      context.commit('updateStateOrderInfoLoadingFailed', false);
      clearTimeout(this.loadOrderInfoTimer);
      this.loadOrderInfoTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}api/orders/${orderId}`, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then((response) => {
            // console.log(response.data);
            context.commit('updateOrderInfo', response.data);
          })
          .catch((error) => {
            context.commit('updateStateOrderInfoLoadingFailed', true);
            // console.log(error);
          })
          .then(() => {
            context.commit('updateStateOrderInfoLoading', false);
          })
          .finally(() => {
            // always executed
          });
      }, 500);
    },
    loadCart(context) {
      context.commit('updateStateCartLoading', true);
      context.commit('updateStateCartLoadingFailed', false);

      clearTimeout(this.loadCartTimer);
      this.loadCartTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}api/baskets`, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then((response) => {
            // handle success

            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccesKey', response.data.user.accessKey);
              // console.log('2',response.data.user.accessKey )
              context.commit('updateUserAccesKey', response.data.user.accessKey);
            }

            context.commit('updateCartProductsData', response.data.items);

            context.commit('syncCartProducts');
          })
          .catch((error) => {
            context.commit('updateStateCartLoadingFailed', true);
            // console.log(error);
          })
          .then(() => {
            context.commit('updateStateCartLoading', false);
          })
          .finally(() => {
            // always executed
          });
      }, 500);
    },
    addProductToCart(context, {
      productId,
      count,
      colorId,
      sizeId
    }) {
      return (new Promise((resolve) => {
          setTimeout(resolve, 500);
        }))
        .then(() => axios.post(`${API_BASE_URL}api/baskets/products`, {
            productId,
            quantity: count,
            colorId,
            sizeId
          }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          })
          .then((response) => {
            context.commit('updateCartProductsData', response.data.items);

            context.commit('syncCartProducts');
            // handle success
          })
          .catch((error) => {
            // console.log(error);
            context.commit('updateStateCartLoadingFailed', true);
          })
          .then(() => {
            context.commit('updateStateCartLoading', false);
          })
          .finally(() => {
            // always executed
          }));
    },
    async updateCartProductAmount(context, {
      productIds,
      count,
    }) {
      try {
        const response = await axios
          .put(`${API_BASE_URL}api/baskets/products`, {
            basketItemId: productIds,
            quantity: count,
          }, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          });
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      } catch (error) {
        // console.log(error);
      }
    },

    async deleteProductfromCart(context, basketItemId) {
      context.commit('deleteProductfromCart', basketItemId);
      try {
        const response = await axios
          .delete(`${API_BASE_URL}api/baskets/products`, {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
            data: {
              basketItemId: basketItemId,
            },
          });
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      } catch (error) {
        // console.log(error);
      }
    },
  },

});

export default store;
