<template>
  <base-preloader v-if="productLoad === true" />
  <div v-else-if="productLoadFailed === true">Ошибка загрузки товаров...</div>
  <ul class="catalog__list" v-else>
    <li class="catalog__item" v-for="product in products" :key="product.id">
      <catalog-item :product="product" />
    </li>
  </ul>
</template>
<script setup>
import { toRefs } from 'vue'
import CatalogItem from '@/components/layout/CatalogItem.vue';
import BasePreloader from '@/components/ui/BasePreloader.vue'
const props = defineProps({
  products: {
    type: Object,
    required: true
  },
  productLoad: {
    type: Object
  },
  productLoadFailed: {
    type: Object
  }
})

const { products, productLoad, productLoadFailed } = toRefs(props);
</script>
<style>
.catalog__item {
  position: relative;
}
</style>
