<template>
  <label class="check-list__label" :for="value">
    <input class="check-list__check sr-only" type="checkbox" :name="props.name" @input="checkClick($event)" :value="id"
      :id="value">
    <span class="check-list__desc">
      {{ title }}
      <span>({{ productsCount }})</span>
    </span>
  </label>
</template>
<script setup>
const emits = defineEmits(['updateCheckboxGroup'])
const props = defineProps({
  name: {
    type: String,
    default: ''
  },
  id: {
    type: Number,
    default: 0
  },
  value: {
    type: String,
    default: ''
  },
  title: {
    type: String,
    default: ''
  },
  productsCount: {
    type: Number,
    default: 0
  },
})
const checkClick = (event) => {
  emits('updateCheckboxGroup', { checkedId: props.id, checked: event.target.checked, name: props.name })
}
</script>
