<template>
  <main class="content container" v-if="$store.state.cartLoading">
    <base-preloader/>
  </main>
  <main class="content container" v-else-if="$store.state.cartLoadingFailed">
    При загрузке возникли ошибки...</main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>
      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ totalProductCount }}
        </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <cart-item v-for="item in productCalc" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice }} ₽</span>
          </p>
          <router-link :to="{ name: 'order' }" custom v-slot="{ navigate }"  v-if="totalProductCountNum > 0">
            <button @click="navigate" @keypress.enter="navigate" role="link" class="cart__button button button--primery" type="button" :disabled="!totalPrice">Оформить заказ</button>
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>
<script setup>
import CartItem from '@/components/layout/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import { useStore } from 'vuex';
import { computed } from 'vue';
import selectionEnd from '@/helpers/selectionEnd.js';
import BasePreloader from '../components/ui/BasePreloader.vue'

const $store = useStore();
const productCalc = computed(() => $store.getters['cartDetailProducts'])
const totalPrice = computed(() => numberFormat($store.getters['cartTotalPrice']))
const totalProductCount = computed(() => selectionEnd($store.getters['cartTotalCountProduct'], ['товар', 'товара', 'товаров']))
const totalProductCountNum = computed(() => $store.getters['cartTotalCountProduct'])
</script>
<style scoped>
.cart__button {
  text-align: center;
}
</style>
