<template>
  <transition name="fade" mode="out-in" appear>
    <div class="catalog__item-wrap">
      <router-link class="catalog__pic" :to="{ name: 'product', params: { id: product.id, colorid: currentColor } }">
        <img :src="imgUrl" :alt="product.title" class="productImg">
      </router-link>

      <h3 class="catalog__title">
        <router-link :to="{ name: 'product', params: { id: product.id, colorid: currentColor } }">
          {{ product.title }}
        </router-link>
      </h3>

      <span class="catalog__price">
        {{ productPriceFormat }} ₽
      </span>
      <base-radiolist :product-color="product.colors" @radioClick="currentColor = $event" :product-id="product.id"
      v-model.number="currentColor" :current-color="currentColor" :class-list="['colors', 'colors-black']" :class-item="['colors__item']"
      :class-label="['colors__label']" :class-input="['colors__radio', 'sr-only']" :class-span="['colors__value']"/>
      <button class="catalog__btn-add" @click.prevent="openQuickView">
        <svg width="40" height="31" fill="green">
          <use xlink:href="#icon-cart"></use>
        </svg>
      </button>
      <base-modal v-model:open="isQuickViewOpen" >
        <item-quick-view :productId="product.id" :colorId="currentColor" @closeModal="closeQuickView" />
      </base-modal>
    </div>
  </transition>
</template>
<script setup>
import BaseModal from '@/components/layout/BaseModal.vue'
import ItemQuickView from '@/components/layout/ItemQuickView.vue'
import { ref, computed, toRefs } from 'vue';
import numberFormat from '@/helpers/numberFormat';
import imgEmty from '@/assets/img/ab21d54aac6d7640e57e.png'
import { useStore } from 'vuex';
import BaseRadiolist from '@/components/layout/BaseRadiolist.vue';
const $store = useStore();

const props = defineProps({
  product: {
    type: Object,
    required: true
  }
})

const isQuickViewOpen = ref(false)
const openQuickView = () => {
  isQuickViewOpen.value = true
}
const closeQuickView = () => {
  isQuickViewOpen.value = false
}

const emits = defineEmits(['update:currentColor'])
const { product } = toRefs(props);

const currentColor = ref(product.value.colors[0].color.id);

const productPriceFormat = computed(() => {
  return numberFormat(product.value.price);
})

const checkClick = (event) => {
  emits('update:currentColor', event.target.value);
}

const imgUrl = computed(() => {
  const thisColor = product.value.colors.find(c => c.color.id === currentColor.value);
  if (thisColor.gallery && thisColor.gallery[0] && thisColor.gallery[0].file) {
    return thisColor.gallery[0].file.url
  }
  return imgEmty;
})
</script>
<style scoped>
.productImg {
  object-fit: contain;
}

.colors__radio.checked~.colors__value::before {
  border-color: var(--border-color);
}

.catalog__btn-add {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: none;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.fade-enter-active,
.fade-leave-active {
  animation: bounce .5s;
}

.fade-enter,
.fade-leave-to

/* .fade-leave-active до версии 2.1.8 */
  {
  animation: bounce .5s reverse;
}

@keyframes bounce {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}
</style>
