<template>
  <ul class="check-list">
    <li class="check-list__item" v-for="checkBox in props.arrayValue" :key="checkBox.id">
      <BaseCheckbox :name="props.arrayName" :id="checkBox.id" :value="checkBox.code" :title="checkBox.title"
        :productsCount="checkBox.productsCount" @updateCheckboxGroup="check" />
    </li>
  </ul>
</template>
<script setup>
import BaseCheckbox from '@/components/ui/BaseCheckbox.vue';
import { ref, toRefs, computed } from 'vue'
const props = defineProps({
  arrayName: {
    type: String,
  },
  arrayValue: {
    type: Array,
  }
})

const { arrayName, arrayValue } = toRefs(props);
const emits = defineEmits(['check-on']);
let updateValue = [];

const check = (params) => {
  if (params.checked) {
    updateValue.push(params.checkedId)
  }
  else {
    updateValue = updateValue.filter(item => item != params.checkedId);
  }
  emits('check-on', updateValue);
}
</script>
