<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" href="#" :class="{ 'pagination__link--disabled': page === 1 }"
        @click.prevent="paginate(page - 1)" aria-label="Предыдущая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link" href="#" :class="{ 'pagination__link--current': pageNumber === page }"
        @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': page === pages }"
        @click.prevent="paginate(page + 1)" href="#" aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>
<script setup>
import { computed, toRefs } from 'vue'
const props = defineProps({
  page: {
    type: Number,
    default: 1
  },
  countAllProducts: {
    type: Number,
    default: 0
  },
  countInPage: {
    type: Number,
    delault: 0
  }
})
const emits = defineEmits(['change-page'])
const { page, countAllProducts, countInPage } = toRefs(props);

const pages = computed(() => {
  return Math.ceil(countAllProducts.value / countInPage.value)
})

function paginate(page) {
  if (page >= 1 && page <= this.pages) {
    emits('change-page', page);
  }
}

</script>
