<template>
  <fieldset class="form__block">
    <legend class="form__legend">Цена</legend>
    <label class="form__label form__label--price">
      <input class="form__input" type="text" name="min-price" @change="send" v-model="selectedPriceFrom">
      <span class="form__value">От</span>
    </label>
    <label class="form__label form__label--price">
      <input class="form__input" type="text" name="max-price" @change="send" v-model="selectedPriceTo">
      <span class="form__value">До</span>
    </label>
  </fieldset>

  <fieldset class="form__block">
    <legend class="form__legend">Категория</legend>
    <label class="form__label form__label--select">
      <select class="form__select" name="category" v-model="selectedCategory" @change="send">
        <option value="0">Все категории</option>
        <option :value="category.id" v-for="category in categories.items" :key="category.id">{{ category.title }}
        </option>
      </select>
    </label>
  </fieldset>

  <fieldset class="form__block">
    <legend class="form__legend">Материал</legend>
    <base-checkboxlist arrayName="material" :arrayValue="materials.items" @check-on="selectedMaterials = $event"
      @change="send" />
  </fieldset>

  <fieldset class="form__block">
    <legend class="form__legend">Коллекция</legend>
    <base-checkboxlist arrayName="collection" :arrayValue="seasons.items" @check-on="selectedSeasons = $event"
      @change="send" />
  </fieldset>
</template>
<script setup>
import { ref, toRefs } from 'vue'
import BaseCheckboxlist from '@/components/layout/BaseCheckboxlist.vue';

const props = defineProps({
  categories: {
    type: Object,
  },
  materials: {
    type: Object,
  },
  seasons: {
    type: Object,
  },
  selectCategory:{
    type:Number,
  }
})

const{categories,materials,seasons, selectCategory }= toRefs(props)
const emit = defineEmits(['send']);
const selectedPriceFrom = ref(null);
const selectedPriceTo = ref(null);
const selectedCategory = ref(selectCategory);
const selectedMaterials = ref([]);
const selectedSeasons = ref([]);
const send = () => {
  emit('send', [selectedPriceFrom, selectedPriceTo, selectedCategory, selectedMaterials, selectedSeasons])
}
</script>
