import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import {
  ref,
  computed
} from 'vue';
import {
  API_BASE_URL
} from '@/config';
import {
  useStore
} from 'vuex';
import {
  useRouter
} from 'vue-router';


export function sendOrder() {
  const $router = useRouter();
  const $store = useStore();
  const formError = ref({})
  const formErrorMessange = ref('')

  const fetchStatus = {
    isSend: ref(false),
    isSendFailed: ref(false),
    isSending: ref(false),
  }
  const fetchOrder = (formData) => {
    //console.log(formData)
    axios.post(`${API_BASE_URL}api/orders`,
        formData
      , {
        params: {
          userAccessKey: $store.state.userAccessKey,
        },
      })
      .then((response) => {
        $store.commit('resetCart');
        $store.commit('updateOrderInfo', response.data);
        // this.reset();
        fetchStatus.isSend.value = false;
        fetchStatus.isSendFailed.value = false;
        $router.push({
          name: 'orderInfo',
          params: {
            id: response.data.id
          }
        });
      })
      .catch((er) => {
        fetchStatus.isSend.value = false;
        fetchStatus.isSendFailed.value = true;
      //  console.log(er.response);
        formError.value = er.response.data.error.request || {};
        formErrorMessange.value = er.response.data.error.message;

        // console.log(er);
      })
      .then(() => {
        fetchStatus.isSending = true;
      });
  }

  return {
    formError,
    formErrorMessange,
    fetchOrder,
    status: fetchStatus
  };
}
/*
получение информации о способах доставки
*/
export function deliveryList() {
  const fetchStatus = {
    isLoading: ref(false),
    isLoadingFailed: ref(false)
  }

  const deliveries = ref([]);

  fetchStatus.isLoading.value = true;
  fetchStatus.isLoadingFailed.value = false;
  axios.get(`${API_BASE_URL}api/deliveries`)
    .then((response) => {
      const r = response.data.map(item =>
        ({
          ...item,
          sum: parseInt(item.price),
          deliveryFor: `delivery_${item.id}`
        })
      )
      deliveries.value = r;
    })
    .catch((error) => {
      fetchStatus.isLoadingFailed.value = true;
    })
    .then(() => {
      fetchStatus.isLoading.value = false;
    })

  return {
    deliveries,
    statusDelivery: fetchStatus,
  };
}
/*
получение способов оплаты по id доставки
*/
export function payList(deliveryTypeId) {
  const fetchStatus = {
    isLoading: ref(false),
    isLoadingFailed: ref(false)
  }

  const payments = ref([]);

  fetchStatus.isLoading.value = true;
  fetchStatus.isLoadingFailed.value = false;
  axios.get(`${API_BASE_URL}api/payments?deliveryTypeId=${deliveryTypeId}`)
    .then((response) => {
      const r = response.data.map(item =>
        ({
          ...item,
          payFor: `pay_${item.id}`,
          keys: `pay_${item.id}_${deliveryTypeId}`
        })
      )
      payments.value = r
    })
    .catch((error) => {
      fetchStatus.isLoadingFailed.value = true;
      //console.log(error)
    })
    .then(() => {
      fetchStatus.isLoading.value = false;

    })

  return {
    payments,
    statusPay: fetchStatus,
  };
}
