import { createRouter, createWebHashHistory } from 'vue-router';
import IndexPage from '@/pages/IndexPage.vue'
import ItemPage from '@/pages/ItemPage.vue';
import CartPage from '@/pages/CartPage.vue';
import OrderPage from '@/pages/OrderPage.vue';
import OrderInfoPage from '@/pages/OrderInfoPage.vue';
import notFoundPage from '@/pages/notFoundPage.vue';

import config from '../../vue.config';

const routes = [
  { name: 'main', component: IndexPage, path: '/' },
  { name: 'main1', component: IndexPage, path: '/:categoryId' },
  { name: 'product', component: ItemPage, path: '/product/:id/:colorid' },
  { name: 'cart', component: CartPage, path: '/cart' },
  { name: 'order', component: OrderPage, path: '/order' },
  { name: 'orderInfo', component: OrderInfoPage, path: '/order/:id' },
  { name: 'notFound', component: notFoundPage, path: '/:patchMatch(.*)*' },

];

const router = createRouter({
  history: createWebHashHistory(config.publicPath),
  routes,
});

export default router;
