<template>
  <div class="product__counter form__counter">
    <button type="button" aria-label="Убрать один товар"
    @click.prevent="count--">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <label for="count">
      <input type="text" v-model.number="count" name="count" id="count">
    </label>
    <button type="button" aria-label="Добавить один товар"
    @click.prevent="count++">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>
<script setup>
import { useStore } from 'vuex';
import {computed, toRefs} from 'vue'
const $store = useStore();
const props = defineProps({
  item: {
    type: Object,
  },
})

const {item}= toRefs(props)

const count = computed({
  get() {
    return item.value.count
  },
  set(val) {
    if (val >= 1) {
      updateCartProductAmount({ productIds: item.value.basketItemId, count: val });
    }
  }
})

const updateCartProductAmount=({productIds, count})=>{
    $store.dispatch('updateCartProductAmount', {productIds, count});
}
</script>
