<template>
  <h3 class="cart__title">Оплата</h3>
  <ul class="cart__options options" v-if="isShow">
    <li class="options__item" v-for="item in payments" :key="item.keys">
      <label class="options__label" :for="item.payFor">
        <input class="options__radio sr-only" type="radio" :id="item.payFor" name="pay" :value="item.id"
          @click="check($event)">
        <span class="options__value">
          {{ item.title }}
        </span>
      </label>
    </li>
  </ul>
</template>
<script setup>
import { payList } from '../../hooks/useOrder.js'
import { toRefs, ref, watch, nextTick, onBeforeMount } from 'vue'
const props = defineProps({
  deliveryId: {
    type: Number,
  }
})
const emit = defineEmits(['update'])
const check = (params) => {
  emit('update', params.target.value);
}

onBeforeMount(() =>  {
 //        console.log('1')
      })

const { deliveryId } = toRefs(props)
let isShow = ref(false);
const { payments, statusPay } = payList(deliveryId.value);
nextTick(() => {
    isShow.value = statusPay.isLoading
  })

watch(deliveryId, () => {
  isShow.value = false
  const { payments, statusPay } = payList(deliveryId.value);
  nextTick(() => {
    isShow.value = statusPay.isLoading
  })
})
</script>
