<template>
  <ul class="tabs">
    <li v-for="tab in names" :key="tab.name"
      :class="['tabs__item', { 'tabs__link--current': tab.name === selectedTab }]" @click="clickOnTabs(tab.name)"> <span
        class="tabs__link">
        {{ tab.label }}
      </span>
    </li>
  </ul>
  <div class="item__content">
    <slot />
  </div>
</template>
<script setup>
const props = defineProps({
  names: {
    type: Array,
    required: true
  },
  selectedTab: {
    type: String,
    required: false
  }
}
)
const emits = defineEmits(['changeTab'])
const clickOnTabs = (tabsName) => {
  emits('changeTab', tabsName)
}
</script>
<style scoped>
.tabs__link--current .tabs__link {
  color: #222;
}
</style>
