<!-- eslint-disable no-irregular-whitespace -->
<!-- eslint-disable max-len -->
<template>
  <div class="content" v-if="statusLoad === true">
    <base-preloader />
  </div>
  <div class="content" v-else-if="productLoadFailed === true">Ошибка загрузки товаров...</div>
  <transition name="slide-fade">
    <div class="content" v-if="statusLoad === false && productLoadFailed === false">
      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img width="570" height="570" :src="currentImg" :alt="product.title">
          </div>
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">
            {{ product.title }}
            <br /> ({{ product.pricePretty }} ₽)
            <br/>
            <strong>
              <router-link :to="{ name: 'product', params: { id: product.id, colorid: currentColor } }">
                Подробнее...
              </router-link>
            </strong>
          </h2>
          <div class="item__form">
            <form class="form" action="#" method="POST" @submit.prevent="addToCart">
              <div class="item__row item__row--center">
                <fieldset class="form__block">
                  <legend class="form__legend">Количество</legend>
                  <base-addremoveitem :counter-value="countProduct" @update="countProduct = $event"
                    @click="productAdded = false" v-model.number="countProduct" />
                </fieldset>
              </div>


              <div class="item__row item__row--center">
                <fieldset class="form__block">
                  <legend class="form__legend">Цвет</legend>
                  <base-radiolist :product-color="product.colors" @radioClick="currentColor = $event"
                    :product-id="product.id" v-model.number="currentColor" :current-color="currentColor"
                    :class-list="['colors', 'colors-black']" :class-item="['colors__item']"
                    :class-label="['colors__label']" :class-input="['colors__radio', 'sr-only']"
                    :class-span="['colors__value']" @click="productAdded = false" />
                </fieldset>
              </div>
              <div class="item__row item__row--center">
                <fieldset class="form__block block-size">
                  <span class="form__error" v-if="error">Вы не выбрали размер</span>
                  <legend class="form__legend">Размер</legend>
                  <label class="form__label form__label--small form__label--select">
                    <select class="form__select" name="category" v-model="currentSize"
                      @click="productAdded = false; error = false">
                      <option value="0">Выберите размер</option>
                      <option v-for="size in product.sizes" :value="size.id" :key="size.id">{{ size.title }}</option>
                    </select>
                  </label>
                </fieldset>
              </div>

              <button class="item__button button button--primery" type="submit">
                <transition name="fade">
                  <span v-if="productAddSending">
                    Добавляем товар в корзину
                  </span>
                </transition>
                <transition name="fade">
                  <span v-if="productAdded">
                    Товар добавлен в корзину
                  </span>
                </transition>
                <span v-if="!productAdded && !productAddSending">В корзину</span>
              </button>
            </form>
          </div>
        </div>
      </section>
    </div>
  </transition>
</template>
<script setup>

import { productItem } from '@/hooks/useProduct.js';
import { useStore } from 'vuex';
import { ref, computed, toRefs, watch, defineEmits } from 'vue';
import { useRoute } from 'vue-router';
import BaseAddremoveitem from '@/components/layout/BaseAddremoveitem.vue';
import imgEmty from '@/assets/img/ab21d54aac6d7640e57e.png';
import BaseRadiolist from '@/components/layout/BaseRadiolist.vue';
import BasePreloader from '@/components/ui/BasePreloader.vue'
const $route = useRoute();
const $store = useStore();

const { productData, category, loadProduct, status: productStatus } = productItem();
const props = defineProps({
  productId: {
    type: [Number, String],
    required: true,
  },
  colorId: {
    type: [Number, String],
    required: true,
  },
})
const { colorId, productId } = toRefs(props);
loadProduct(productId.value);
const emit = defineEmits(['closeModal']);
let statusLoad = ref(false);
statusLoad = productStatus.isLoading;
let productLoadFailed = ref(false);
productLoadFailed = productStatus.isLoadingFailed;

const product = computed(() => {
  const res = productData.value
    ? productData.value
    : [];
  return res;
})
const countProduct = ref(1);
const currentColor = ref(colorId)
const currentSize = ref(null);
const currentImg = ref(null);
const error = ref(false)

watch(productStatus.isLoading, () => {
  currentImg.value = product.value.colors.find(c => c.color.id === currentColor.value).gallery ? product.value.colors.find(c => c.color.id === currentColor.value).gallery[0].file.url : imgEmty;
  currentSize.value = 0;
})

watch(currentColor, () => {
currentImg.value = product.value.colors.find(c => c.color.id === currentColor.value).gallery ? product.value.colors.find(c => c.color.id === currentColor.value).gallery[0].file.url : imgEmty;
})


const productAdded = ref(false);
const productAddSending = ref(false);

const addToCart = () => {
  if (currentSize.value === 0) {
    error.value = true;
    return
  }
  productAdded.value = false;
  productAddSending.value = true;
  $store.commit('updateStateAddProductInCart', true);
  $store.dispatch('addProductToCart', {
    productId: product.value.id,
    count: countProduct.value,
    colorId: currentColor.value,
    sizeId: currentSize.value
  }).then(() => {
    productAdded.value = true;
    productAddSending.value = false;
    $store.commit('updateStateAddProductInCart', false);
    emit('closeModal');
  });
};


</script>
<style scoped>
.materials {
  display: block;
}

.block-size {
  position: relative;
}

.slide-fade-enter-active {
  transition: all 0.3s ease-out;
}

.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  transform: translateX(20px);
  opacity: 0;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>

